@media (min-height: 1050px) and (min-width: 1900px) {
  .sub__intro {
    margin-bottom: -9%;
  }
}

@media screen and (max-width: 1250px) {
  .specs .specs__bot {
    gap: 10px;
  }
}

@media screen and (max-width: 1199px){
  // fz
  h1, .h1 {
    font-size: 120px;
  }
  h2, .h2 {
    font-size: 90px;
  }
  h3, .h3 {
    font-size: 75px;
  }
  h4, .h4 {
    font-size: 58px;
  }
  .sub__intro-text {
    font-size: 40px;
  }
  .toggler .toggler__btn {
    font-size: 14px;
  }
  .personal .personal__content-title {
    font-size: 90px;
  }
  .personal__content-p {
    font-size: 20px;
  }
  .personal__inner-bg img {
    object-fit: contain;
  }
  .features .features__desc {
    font-size: 20px;
    line-height: 29px;
  }
  .reviews .reviews__content .review .review__message {
    font-size: 20px;
    line-height: 29px;
  }
  .features .features__block .features__block-tab .features__block-tab--body p {
    font-size: 20px;
    line-height: 29px;
  }
  .features .features__banner .features__banner-text .feature__banner-desc {
    font-size: 20px;
    line-height: 29px;
  }
  .returns .returns__inner .returns__box-text {
    font-size: 20px;
    line-height: 29px;
  }
  .features .features__block .features__block-desc {
    font-size: 20px;
    line-height: 29px;
  }
  .returns .returns__inner .returns__box-title {
    font-size: 38px;
  }
  .reviews .reviews__content .review .review__name {
    font-size: 16px;
    margin-bottom: 5px;
  }
  .about__text-column {
    font-size: 20px;
    line-height: 29px;
  }
  .pricing__product-name {
    font-size: 38px;
  }
  .pricing__product-kit,
  .product__card-kit {
    font-size: 18px;
  }
  // else
  .specs__inner {
    padding: 120px 0;
  }
  .specs .specs__content {
    grid-template-columns: .7fr .8fr;
  }
  .specs__bot--image {
    flex: 0 0 80%;
  }
  .specs__bot--arrow.aside-arrow {
    flex: 0 0 10%;
  }
  .product__card-img img {
    object-fit: contain;
  }
  .pricing__product-details {
    grid-template-columns: 70px 70px;
  }
  .specs .specs__content {
    gap: 20px;
  }
  .specs .specs__list {
    padding: 0;
  }
  .features__inner {
    padding: 40px 0;
  }
  .features .features__block.text-to-left .features__block-image {
    margin-right: -60px;
  }
  .features .features__block.text-to-right .features__block-image {
    margin-left: -60px;
  }
  .returns .returns__inner .returns__box {
    padding: 50px 70px 50px;
  }
  .personal__content {
    padding: 290px 0px 100px 0;
  }
  .personal__inner {
    min-height: 1200px;
    background: transparent;
  }
  .pricing__product-main-img {
    max-width: 50%;
    img {
      object-fit: contain;
    }
  }
  //footer

  .footer__contact .footer__phone {
    font-size: 30px;
  }
  .footer__top {
    padding: 0 0 60px 0;
  }
  .footer__mail {
    font-size: 20px;
  }
  .footer__menu li {
    margin-bottom: 20px;
  }
  .footer__menu li a {
    font-size: 18px;
  }
  .app_block-box .app_box-content .app_box-desc {
    flex: auto;
  }
  .specs__bot--image {
    img {
      object-fit: contain;
    }
  }
  .specs__bot--arrow {
    width: 80%;
    svg {
      width: 100%;
    }
  }
}
@media screen and (max-width: 1025px) {
  .reviews .reviews__content .review .review__message {
    font-size: 18px;
  }
  .returns .returns__inner .returns__box-text {
    font-size: 16px;
    line-height: 24px;
  }
  .returns .returns__inner .returns__box-title {
    font-size: 30px;
    margin-bottom: 10px;
  }
  .app_block-box {
    padding: 60px 60px 0 60px;
  }
}

@media screen and (max-width: 991px){
  .returns .returns__inner {
    grid-template-columns: 1fr;
  }
  .header__inner {
    align-items: center;
  }
  .header__mobile-icon {
    display: block;
  }
  .header__mobile-icon {
    flex: 0 0 28px;
    height: 28px;
    padding: 10px 3px;
    display: flex;
    gap: 8px;
    flex-direction: column;
    span {
      display: block;
      width: 100%;
      min-height: 1px;
      background-color: #000;
    }
  }
  .header__nav {
    display: flex;
    justify-content: center;
    transition: 1s;
    opacity: 0;
    visibility: hidden;
    .nav__list {
      display: flex;
      flex-direction: column;
    }
  }
  .header .nav__list .nav__item.mobile-logo {
    display: block;
    a {
      font-size: 42px;
      color: $colorGreen;
    }
  }

  .header .header__buy-btn .btn .btn__text {
    display: none;
  }
  .header .header__buy-btn .btn {
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 50%;
  }
  .features .features__block {
    margin-bottom: 60px;
  }
  .features .features__banner .features__banner-background {
    padding: 30px 0;
  }
  .header .header__buy-btn .btn .btn__icon-wrap {
    padding: 0;
  }
  .header__nav {
    position: fixed;
    width: 100%;
    height: 100%;
    left: -120%;
    transition: 1s;
    top: 0;
  }
  .intro .intro__title {
    margin-bottom: 90px;
  }
  .container {
    padding: 0 20px;
  }
  .road__svg-model {
    transform: scale(1.5);
  }
  .specs .specs__content {
    grid-template-columns: 1fr 0.8fr;
    justify-content: space-between;
  }
  .features .features__block.text-to-left .features__block-image {
    margin-right: -20px;
  }
  .features .features__block.text-to-right .features__block-image {
    margin-left: -20px;
  }
  .specs .specs__content {
    grid-template-columns: .5fr .8fr;
  }
  .specs__list-item--value {
    font-size: 16px;
  }
  .specs .specs__list .specs__list-item {
    font-size: 16px;
  }
  .specs .specs__bot .specs__bot--arrow span {
    font-size: 14px;
  }
  h4, .h4 {
    font-size: 40px;
    line-height: 46px;
  }
  .reviews .reviews__content .reviews__item-text {
    font-size: 16px;
    line-height: 26px;
  }
  .pricing .pricing__special-product {
    padding: 25px;
  }
  .pricing__product-main-img img {
    height: auto;
  }
  .pricing__product-details .pricing__detail .pricing__detail-count {
    font-size: 10px;
    left: 5px;
    top: 5px;
  }
  .about__text-column {
    padding-left: 40px;
    padding-right: 40px;
  }
  .pricing__product-details {
    left: 25px;
    top: 25px;
    grid-template-columns: 50px 50px;
  }

}
@media screen and (max-width: 767px){
  h2, h2,.personal .personal__content-title {
    font-size: 58px;
    line-height: 60px;
  }
  .intro__subtitle {
    margin-bottom: 10px;
  }
  .intro__media {
    transform: scale(1.7);
  }
  .scroll-to-bottom-text {
    font-size: 12px;
  }
  .sub__intro-box {
    gap: 25px;
  }
  .personal__content {
    overflow: hidden;
    padding: 100px 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 200px;

  }
  .personal__inner {
    min-height: auto;
  }
  .road__title {
    margin-bottom: 30px;
  }
  .personal__content-scroll-to {
    .scroll-to-bottom-text {
      display: none;
    }
  }
  .personal .personal__content-title {
    max-width: 300px;
  }
  .personal__inner-bg {
    img {
      width: 600px;
      height: 600px;
    }
  }
  .easy__inner-bot-bottom {
    img {
      width: 100%;
    }
  }
  .sub__intro-text {
    font-size: 28px;
    line-height: 34px;
  }
  h1, .h1 {
    font-size: 90px;
    line-height: 86px;
  }
  .features .features__num {
    display: none;
  }
  .features .features__intro-info {
    justify-content: center;
    margin-top: 10px;
  }
  .features__desc {
    text-align: center;
    width: 100%;
  }
  .features .features__inner .features__block .features__block-inner {
    flex-direction: column;
  }
  .features__intro-car svg{
    height: auto;
    max-width: 500px;
  }
  .features .features__intro {
    margin-bottom: 60px;
  }
  .features .features__intro-bot {
    top: 0;
    max-width: 350px;
    height: 300px;
  }
  .features__block-tab--head,
  .features__block-text {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    text-align: center;
  }
  .app_block-box .app_box-content {
    flex-direction: column;

  }
  .app_block-box .app_box-content .app_box-desc .app_box-text {
    text-align: center;
    font-size: 16px;
    line-height: 24px;
  }
  .app_block-box .app_box-content .app_box-desc .app_box-markets {
    justify-content: center;
  }
  .app_block-box .app_box-content .app_box-title {
    flex: auto;
    font-size: 40px;
    line-height: 40px;
  }
  .features__banner-background {
    flex-direction: column;
  }
  .features .features__banner .features__banner-text .features__banner-title {
    margin-bottom: 90%;
    font-size: 58px;
    line-height: 60px;
  }
  .features .features__banner .features__banner-text .feature__banner-desc {
    font-size: 16px;
    line-height: 26px;
  }
  .features .features__banner .features__banner-background .background__part-2 {
    position: inherit;
    right: 0;
    top: 0;
    img {
      object-fit: contain;
      height: 100%;
    }
  }
  .app_block-box {
    margin-bottom: 40px;
  }
  h3, .h3 {
    font-size: 40px;
    line-height: 40px;
  }
  .reviews .reviews__title {
    margin-bottom: 30px;
  }
  .reviews .reviews__content .review .review__name {
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 2px;
    align-items: center;
  }
  .reviews .reviews__content {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(1, 1fr);
  }
  .reviews .reviews__content .center-col {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 28px;
    row-gap: 14px;
  }
  .center-col { grid-area: 1 / 1 / 2 / 3; }
  .left-messages { grid-area: 2 / 1 / 4 / 2; }
  .right-messages  { grid-area: 2 / 2 / 4 / 3; }
  .reviews .reviews__content .review .review__message {
    font-size: 14px;
    line-height: 20px;
    padding: 12px 20px;
  }
  .specs .specs__content {
    display: flex;
    flex-direction: column-reverse;
    grid-template-columns: auto;
  }
  .specs .specs__bot {
    margin: 0 auto;
    justify-content: center;
    max-width: none;
  }
  .pricing .pricing__special-product {
    grid-template-columns: 1fr;
    grid-template-rows: 280px 1fr;
  }
  .pricing__product-images {
    display: flex;
    justify-content: center;
  }
  .pricing__product-main-img img {
    height: 100%;
  }
  .product__card-img img {
    height: auto;
  }
  .product__card-title {
    font-size: 28px;
    line-height: 34px;
    margin-bottom: 20px;
  }
  .product__card-kit {
    font-size: 14px;
    line-height: 20px;
  }
  .product__card-price {
    font-size: 20px;
    line-height: 24px;
  }

  .features__banner-background--part img {
    object-fit: contain;
  }
  .pricing__product-main-img {
    position: relative;
    left: -22%;
    max-width: 400px;
    top: -84px;
    height: 375px;
  }
  .product-grid--3 {
    grid-template-columns: 1fr 1fr;
  }
  .about__inner {
    margin-top: 0;
  }
  .about__title {
    display: none;
  }
  .about__text-columns {
    flex-direction: column;
    background-color: $colorGreen;
    padding: 65px 30px;
    .about__text-column {
      padding: 10px;
    }
  }
  .footer__top {
    grid-template-columns: repeat(2, 1fr);
      grid-template-rows: auto;
    gap: 50px;
  }

  .footer__logo {
    grid-area: 1 / 1 / 2 / 2;
  }
  .footer__socials {
    grid-area: 1 / 2 / 2 / 3;
  }
  .footer__menu {
    grid-area: 2/1/4/3;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 25px;
    max-height: 50%;
    li {
      margin-bottom: 0;
    }
  }
  .footer__contact {
    grid-area: 3 / 1 / 4 / 3;
  }
  .footer__companies {
    grid-area: 4 / 1 / 5 / 3;
  }
  .footer__bottom {
    padding: 30px 0;
  }
  .footer__logo a {
    font-size: 34px;
  }
  .footer__bottom p:last-child {
    display: none;
  }
  .footer__contact li:not(:last-child) {
    margin-bottom: 25px;
  }
}
@media screen and (max-width: 575px) {
  .label {
    font-size: 12px;
    line-height: 18px;
  }
  .footer__logo a {
    font-size: 26px;
  }
  .footer__menu li a {
    font-size: 16px;
    line-height: 18px;
  }
  .footer__contact .footer__phone {
    font-size: 32px;
    line-height: 38px;
  }
  .footer__contact .footer__mail {
    font-size: 22px;
    line-height: 26px;
  }
  .product-grid--3 {
    grid-template-columns: 1fr;
  }
  .pricing__product-main-img {
    left: -10%;
  }
  .pricing__product-name {
    font-size: 28px;
    line-height: 34px;
  }
  .pricing__product-kit, .product__card-kit {
    font-size: 14px;
    line-height: 20px;
  }
  .sub__intro-box {
    padding: 40px 30px;
  }
  .toggler {
    width: 256px;
  }
  .sub__intro .toggler {
    width: 100%;
  }
  .personal__content {
    padding: 50px 0;
  }
  .personal__inner-bg img {
    height: 576px;
    object-fit: cover;
    width: 576px;
  }
  .personal__content-p {
    font-size: 16px;
    line-height: 26px;
  }
  .road__title {
    padding: 0 20px;
   font-size: 32px;
    line-height: 42px;

  }
  .features__banner-background--part.background__part-1 img {
    height: auto;
  }
  .features .features__banner .features__banner-text .features__banner-title {
    margin-bottom: 70%;
  }
  .app_block-box {
    padding: 50px 20px 0 20px;
  }
  .app_box-phone {
    padding: 0 20px;
  }
  .returns .returns__inner .returns__box {
    padding: 45px;
    border-radius: 60px;
  }
  .reviews .reviews__content .center-col {
    column-gap: 10px;
  }
  .returns {
    margin-bottom: 60px;
  }
  .specs__bot--image img  {
    width: 267px;
    height: 288px;
  }

  .specs__bot--arrow svg {
    height: 100%;
  }
  .specs__bot--arrow.aside-arrow svg {
    height: auto;
    width: auto;
  }
  .toggler .toggler__btn {
    padding: 13px 10px;
  }
  .returns .returns__inner {
    gap: 20px;
  }
  .desktop-svg {
    display: none;
  }
  .mobile-svg {
    display: block;
  }
  .features__intro-car svg {
    max-width: 370px;
  }
  .features .features__intro-bot {
    max-width: 300px;
  }
  .features .features__intro-bot img {
    max-height: 230px;
  }
  .road__animation {
    overflow: hidden;
  }
  .road__svg-model {
    transform: scale(2);
  }
  .road__animation-rudder {
    transform: scale(1.1);
  }
}