.header {
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 999;
  background: $colorWhite;
  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__logo i {
    font-size: 26px;
  }
  .header__buy-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    .button-wrapper {
      margin-bottom: 0;
    }
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    gap: 10px;
    padding: 30px 0;
  }
  &__nav {
    display: flex;
    align-items: center;
  }
  .nav__list {
    display: flex;
    align-items: center;
    gap: 30px;
    .nav__item a {
      font-size: 14px;
      font-family: Roobert, sans-serif;
    }
  }
  .header__buy-btn {
    .btn {
      background: $colorGrey;
      font-family: Roobert, sans-serif;
      font-size: 14px;
      line-height: 17px;
      padding: 12px 25px;
      display: flex;
      .btn__text {
        padding-right: 10px;
        border-right: 1px solid rgba($colorBlack, 0.3);
      }
      .btn__icon-wrap {
        padding-left: 10px;
      }
    }
  }
}
.header__mobile-icon {
  cursor: pointer;
  span {
    transition: 1s ease-in-out all;
  }
}
.header__mobile-icon.nav-open {
  z-index: 10003;
  span {
    background-color: #fff;
    transform: rotate(45deg) translate(25%);
  }
  span:last-child {
    transform: rotate(-45deg) translate(25%);
  }
}
.mobile-logo {
  display: none;
}
.header__nav.open {
  z-index: 10002;
  opacity: 1;
  visibility: visible;
  left: 0;
  background-color: #000;
  .nav__item a {
    color: #fff;
  }
}