@font-face
	font-family: "svgfont"
	src: url('fonts/svgfont.eot')
	src: url('fonts/svgfont.eot?#iefix') format('eot'), url('fonts/svgfont.woff') format('woff'), url('fonts/svgfont.woff2') format('woff2'), url('fonts/svgfont.ttf') format('truetype')
	font-weight: normal
	font-style: normal

$icon-logo: "\EA01"
$icon-arrow-bottom: "\EA02"
$icon-cart-header: "\EA03"
$icon-battery: "\EA04"
$icon-break: "\EA05"
$icon-load: "\EA06"
$icon-motor: "\EA07"
$icon-speed: "\EA08"
$icon-time: "\EA09"
$icon-tires: "\EA0A"
$icon-way: "\EA0B"
$icon-arrow: "\EA0C"
$icon-facebook: "\EA0D"
$icon-insta: "\EA0E"


%icon
	font-family: "svgfont"
	font-style: normal
	font-weight: normal
	text-rendering: auto
	speak: none
	line-height: 1
	-webkit-font-smoothing: antialiased
	-moz-osx-font-smoothing: grayscale

.icon, [class^="icon-"], [class*=" icon-"]
	@extend %icon
	display: inline-block

.icon-logo:before
	content: $icon-logo
.icon-arrow-bottom:before
	content: $icon-arrow-bottom
.icon-cart-header:before
	content: $icon-cart-header
.icon-battery:before
	content: $icon-battery
.icon-break:before
	content: $icon-break
.icon-load:before
	content: $icon-load
.icon-motor:before
	content: $icon-motor
.icon-speed:before
	content: $icon-speed
.icon-time:before
	content: $icon-time
.icon-tires:before
	content: $icon-tires
.icon-way:before
	content: $icon-way
.icon-arrow:before
	content: $icon-arrow
.icon-facebook:before
	content: $icon-facebook
.icon-insta:before
	content: $icon-insta