$colorWhite: #fff;
$colorBlack: #000;
$colorGreen: #D3FF3F;
$colorGrey: #E4E4E4;
$colorWhite1: #FCFCFF;
.color-green {
	color: $colorGreen;
}
@font-face {
	font-family: Roobert;
	font-weight: 400;
	src: url("./fonts/Roobert-Regular.woff2") format('woff2');
}
@font-face {
	font-family: Neue Montreal;
	font-weight: 300;
	src: url("./fonts/NeueMontreal-Light.woff2") format('woff2');
}

html,
body {
	height: 100%;
}

body {
	color: $colorBlack;
	font-size: 16px;
	font-family: Roobert, sans-serif;
	line-height: 1.25;
	letter-spacing: 0;
}

* {
	outline: none;
	box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-size: 1em;
	line-height: 1;
	margin: 0;
}

img {
	vertical-align: top;
	max-width: 100%;
}

a {
	text-decoration: none;
	transition: none;
	color: $colorBlack;
	&:hover {
		text-decoration: none;
	}
}

ul,
ol {
	list-style-type: none;
	list-style-position: inside;
	padding: 0;
	margin: 0;
}

p {
	margin: 0;
}

button {
	outline: none;

	&:focus {
		outline: none;
	}
}

[class^="icon-"],
[class*=" icon-"] {
	position: relative;
	vertical-align: middle;
}
body.lock {
	overflow: hidden;
}
.wrapper {
	position: relative;
	min-width: 320px;
	min-height: 100%;
	overflow: hidden;
}
i {
	color: currentColor;
}
.main {
	//padding-top: 90px;
}


.container {
	width: 100%;
	margin: 0 auto;
	padding: 0 60px;
}
h1, .h1 {
	font-size: 160px;
	line-height: 1em;
}
h2, .h2 {
	font-weight: 300;
	font-size: 110px;
	line-height: 1em;
}
h3, .h3 {
	font-weight: 300;
	font-size: 90px;
	line-height: 92px;
}
h4, .h4 {
	font-weight: 700;
	font-size: 64px;
	line-height: 80px;
}
h5, .h5 {
	font-weight: 700;
	font-size: 44px;
	line-height: 52px;
}
h6, .h6 {
	font-weight: 700;
	font-size: 32px;
	line-height: 38px;
}
.subtitle {
	font-size: 20px;
	line-height: 30px;
	font-weight: 400;
}
.subtext {
	font-weight: 300;
	font-size: 14px;
	line-height: 22px;
}
button {
	border: 0;
	background: transparent;
	cursor: pointer;
}
.btn {
	font-weight: 500;
	font-size: 12px;
	line-height: 22px;
	z-index: 100000;
	border-radius: 44px;
}





.button-wrapper{
	overflow: hidden;
	position: relative;
	display: inline-block;
	border-radius: 44px;
	margin-bottom: 30px;
}

.su_button_circle{
	background-color: #000;
	border-radius: 1000px;
	position: absolute;
	left:0;
	top:0;
	width: 0px;
	height: 0px;
	margin-left: 0px;
	margin-top: 0px;
	pointer-events: none;
	/*animation-timing-function: ease-in-out; */
}

.btn__text{
	position:relative;
	z-index: 10000;
}

.explode-circle {
	animation: explode 1s forwards;

}
.btn__text {
	transition: 1s;
}
.footer__socials .su_button_circle {
	background-color: $colorGreen;
}
.footer__inner .footer__socials .explode-circle + .btn i {
	color: #000;
}
.header .header__buy-btn .explode-circle + .btn .btn__text {
	border-right: 1px solid #fff;
}
 .btn i {
	transition: 1s;
}
.explode-circle + .btn .btn__text,
.explode-circle + .btn i {
	color: #fff;
}
.desplode-circle{
	animation: desplode 1s forwards;
}

@keyframes explode {
	0% {
		width: 0px;
		height: 0px;
		margin-left: 0px;
		margin-top: 0px;
	}
	100% {
		width: 400px;
		height: 400px;
		margin-left: -200px;
		margin-top: -200px;
	}
}

@keyframes desplode {
	0% {
		width: 400px;
		height: 400px;
		margin-left: -200px;
		margin-top: -200px;
	}
	100% {
		width: 0px;
		height: 0px;
		margin-left: 0px;
		margin-top: 0px;
	}
}

.fade {
	animation-name: fade;
	animation-duration: 1s;
}

@keyframes fade {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

.footer__contact {
	li a {
		transition: .7s all ease-in-out;
		position: relative;
		display: inline-flex;
		&::after {
			position: absolute;
			left: 0;
			bottom: -4px;
			content: "";
			height: 2px;
			width: 100%;
			transform: scaleX(0);
			will-change: transform;
			background: currentColor;
			transform-origin: right center;
			transition: transform .7s cubic-bezier(1,0,0,1);
		}
	}

	li a:hover {
		opacity: 1;
	}
	li a:hover::after {
		transform: scaleX(1);
		transform-origin: left center;
	}
}
.footer__menu li a,
.header__nav li a {
	transition: .7s all ease-in-out;
	position: relative;
	&::after {
		position: absolute;
		left: 0;
		bottom: -2px;
		content: "";
		height: 1px;
		width: 100%;
		transform: scaleX(0);
		will-change: transform;
		background: currentColor;
		transform-origin: right center;
		transition: transform .7s cubic-bezier(1,0,0,1);
	}
	&:hover::after {
		transform: scaleX(1);
		transform-origin: left center;
	}
}