.footer {
  padding-top: 40px;
  background-color: $colorBlack;
}
.footer__inner {
  padding-top: 80px;
  color: #fff;
  a {
    color: #fff;
  }
}
.footer__top {
  display: grid;
  grid-template-columns: .75fr .5fr 1fr;
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 50px;
  grid-row-gap: 50px;
  padding: 0 60px 60px 60px;
  border-bottom: 1px solid rgba(#fff, 0.3);
}
.footer__logo { grid-area: 1 / 1 / 2 / 2; }
.footer__companies { grid-area: 2 / 1 / 3 / 2; }
.footer__menu { grid-area: 1 / 2 / 3 / 3; }
.footer__socials { grid-area: 1 / 3 / 2 / 4; }
.footer__contact { grid-area: 2 / 3 / 3 / 4; }
.footer__logo a {
  font-size: 42px;
  color: $colorGreen;
}
.footer__payments-list {
  margin-bottom: 35px;
}
.footer__socials .button-wrapper {
  width: 100%;
  height: 100%;
}
.footer__menu {
  li {
    margin-bottom: 28px;
  }
  li a {
    font-size: 20px;
    line-height: 18px;
  }
}
.footer__contact {
  .footer__phone{
    font-size: 40px;
    line-height: 48px;


  }
  li a {
    opacity: .3;
  }
  li:not(:last-child) {
    margin-bottom: 40px;
  }
  .footer__mail {
    font-size: 24px;
    line-height: 29px;
    letter-spacing: 0.005em;


  }
}
.footer__socials .btn {
  font-size: 16px;
}
.footer__bottom {
  display: flex;
  justify-content: space-between;
  padding: 60px 0;
  font-size: 16px;
  line-height: 27px;
  opacity: .3;
}